<script>
import { fb } from "@/firebase";

export default {
  name: "Events",
  props: {},
  data() {
    return {
      loggIn: false
    };
  },
  created() {
    this.loginCheck();
  },
  methods: {
    loginCheck() {
      fb.auth().onAuthStateChanged(user => {
        if (user) {
          this.loggIn = true;
        } else {
          this.loggIn = false;
        }
      });
    }
  }
};
</script>

